import styled from 'styled-components';
import React from 'react';
import Header from "./components/Header"
import PlaylistCreation from './components/PlaylistCreation';
import PlaylistManagement from './components/PlaylistManagement';



const AppContainer = styled.div `
display:flex;
flex-direction: column;
width: 100vw;
height: 100vh;
`;





class App extends React.Component{
     state ={
       currentPage:"PlaylistCreation"
     }

     changePage = (currentPage)=>{
       this.setState({currentPage:currentPage})
     }





  render()

  { const currentPage =()=>{
    if(this.state.currentPage === "PlaylistCreation"){
      return <PlaylistCreation/>
    }
    else if(this.state.currentPage === "PlaylistManagement"){
      return<PlaylistManagement/>
    }
  }
    return (
      <AppContainer>
        <Header changePage={this.changePage}></Header>
       {currentPage()}
      </AppContainer>
    );
  }
}

export default App;
