import styled from 'styled-components';
import React from 'react';

const TrackCardContainer =styled.div`
margin: 5px;
display: flex;
align-items: center;

div {
    display: flex;
align-items: center; 
}
`;

const TrackContainer =styled.p`
margin: 15px;
`
const DeleteButton =styled.p`
color:red;
`
const ArtistContainer = styled.p`

`


 const TrackCard =(props)=>{

return(

    <TrackCardContainer>
    <div>
    <TrackContainer>{props.name}</TrackContainer>
    <ArtistContainer>{props.artist}</ArtistContainer>
    <DeleteButton>X</DeleteButton>
    </div>

    <audio controls="controls">
        <source src={props.url} type="audio/ogg"></source>
    </audio>
    </TrackCardContainer>

)
 }

 export default TrackCard