import styled from 'styled-components';
import React from 'react';
import PlaylistCard from '../PlaylistCard';
import axios from 'axios';
import { axiosConfig, baseUrl } from '../../consts';

const PlaylistContainer = styled.div`
display: flex;
flex-direction:column;
align-items: center;

`;




class Playlists extends React.Component{

state ={
    playlists:[]
    
}
componentDidMount =()=>{
    this.fetchPlaylists()
}

fetchPlaylists = () =>{
    axios.get(baseUrl, axiosConfig).then(response =>{
        this.setState({playlists: response.data.result.list})
    }).catch(err =>{
        console.log(err)
    })
}
deletePlaylist = (playlistId)=>{
    axios.delete(`${baseUrl}/${axiosConfig}/${playlistId}/tracks`).then(response =>{this.fetchPlaylists()}).catch(err =>{console.log(err)})
}


render() {
    const ReceivePlaylist=this.state.playlists.map(playlist =>{
        return <PlaylistCard
        key={playlist.id}
        name={playlist.name}
        changePage={this.props.changePage}
        deletePlaylist={this.deletePlaylist}
        playlistId={playlist.id}/>
    })
return(

<PlaylistContainer> 
     Playlists
     {ReceivePlaylist}
</PlaylistContainer>

)
 }
}

 export default Playlists