import styled from 'styled-components';
import React from 'react';
import Playlists from '../Playlists'; 
import PlaylistDetails from '../PlaylistDetails';

const PlaylistManagementConatiner = styled.div`

`;




class PlaylistManagement extends React.Component{
state={
    currentPage:"Playlists",
    playlistId:""
}


changePage =(currentPage,playlistId)=>{
    this.setState({currentPage: currentPage,
        playlistId:playlistId
    
    })
}



render() {
    const currentPage =() => {
        if (this.state.currentPage === "Playlists"){
            return <Playlists
            changePage={this.changePage}/>
        }
        else if(this.state.currentPage === "PlaylistsDetails"){
            return <PlaylistDetails
            changePage={this.changePage}/>
        }
    }
return(

<PlaylistManagementConatiner> 
    {currentPage()}
</PlaylistManagementConatiner>

)
 }
}

 export default PlaylistManagement