import styled, { ThemeConsumer } from 'styled-components';
import React from 'react';
import TrackCard from '../TrackCard';
import axios from 'axios';
import { axiosConfig, baseUrl } from '../../consts';

const PlaylistDetailsContainer = styled.div`
display: flex;
flex-direction: column;

`;

const AddTrackToPlaylist = styled.form`
display: flex;
align-items: center;
width: 100vw;
height: 100px;
justify-content: space-around;

div {
    display: flex;
    flex-direction: column;
}

`




class PlaylistDetails extends React.Component{

    state ={
        tracks:[
        ]
    }
    componentDidMount = ()=>{
        this.fetchTracks()
    }

    fetchTracks = ()=>{
        axios.get(`${baseUrl}/${axiosConfig}/${this.props.playlistId}/tracks`).then(response =>{}).catch(err =>{console.log(err)})
    }

render() {


const tracks = this.state.tracks.map(tracks =>{
    return <TrackCard
    key={tracks.id}
    name={tracks.name}
    artist={tracks.artist}
    url={tracks.url}/>
})

return(

<PlaylistDetailsContainer> 
     <AddTrackToPlaylist>
         <div>
             <label>Nome da musica:   
             </label>
                 <input></input>
         </div>
         <div> 
             <label>Artista:   
             </label>
                 <input></input>
         </div>
         <div>
             <label>Url:   
             </label>
                 <input></input>
         </div>
         <button type="submit">Adicionar musica</button>
     </AddTrackToPlaylist>
     <button onClick={() => this.props.changePage("Playlists", "")}>Voltar Para Lista de Playlists</button>
     <p>Musicas</p>
         {tracks}
</PlaylistDetailsContainer>

)
 }
}

 export default PlaylistDetails