import styled from 'styled-components';
import React from 'react';
import Axios from "axios"
import axios from 'axios';
import { axiosConfig, baseUrl } from '../../consts';


const PlaylistCreationContainer = styled.div`
height: 500px;
display:flex;
flex-direction:column;
`;

const PlaylistCreationForm =styled.form`
height: 100px;
display: flex;
flex-direction: column;
align-items: center;
justify-content: space-around;
`;



 class PlaylistCreation extends React.Component {
    state ={
        inputNameValue: ""
    }


 createPlaylist =(event)=>{
     event.preventDefault()
     const body= {
         name:this.state.inputNameValue

        }
        axios.post(baseUrl, body, axiosConfig).then(response =>{}).catch(err => console.log(err))
        this.setState({inputNameValue: ""})
}


changeInputNameValue =(event)=>{
    this.setState({inputNameValue:event.target.value})

}


    render() {  
return(

<PlaylistCreationContainer>
    <h1>Cadastre sua nova Playlist</h1>
    <PlaylistCreationForm onSubmit={this.createPlaylist}>
    <label>Nova Playlist</label>
    <input placeholder={"Nome da Playlist"} type="text"
    onChange={this.changeInputNameValue}value={this.state.inputNameValue}></input>
    <button>Cadastrar Playlist</button>
    <h2>Playlists Atuais</h2>
    
    </PlaylistCreationForm>
</PlaylistCreationContainer>

)
 }
}

 export default PlaylistCreation